@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  li {
    @apply p-4;
  }

  button {
    @apply text-white border bg-indigo-600 border-indigo-600 hover:bg-transparent hover:text-indigo-600 rounded-md;
  }
}
.gradient-text {
  background-image: linear-gradient(to right, #007bff, #8a2be2, #e74c3c, #f9ca24);
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
}

.smokey-text {
  font-family: 'YourCursiveFont', cursive;
  color: transparent;
  background: linear-gradient(to right, #2d0202, #ec6bd2);
  -webkit-background-clip: text;
  background-clip: text;
}
